export default {
	data() {
		return {
			type: '',
			num: 0,
			count: 5,
			coinAnimation: null,
			detailAnimation: null
		};
	},
	methods: {
		findBoss() {
			wx.cloud.init({
				env: 'test-aed77'
			})
			wx.cloud.callFunction({
				name: 'findBoss'
			}).then(response => {
				console.log(response)
				let { result } = response
				if(result && result.code === 200) {
					uni.navigateTo({
						url: `../boss/boss?json=${JSON.stringify(result.program)}`
					})
				}
			})
		},
		goToStore() {
			uni.navigateTo({
				url: '../store/store'
			})
		},
		startCartoon() {
			let _this = this
			let coinAni = uni.createAnimation({
				duration: 2000,
				timingFunction: 'ease'
			})
			let detailAni = uni.createAnimation({
				duration: 2000,
				timingFunction: 'ease'
			})
			coinAni.translateY(200).opacity(0).step()
			detailAni.translateY(-140).opacity(0).step()
			_this.coinAnimation = coinAni.export()
			_this.detailAnimation = detailAni.export()
		},
		init() {
			let ob = uni.getStorageSync('leftOb')
			this.num = ob
		}
	},
	onLoad(query) {
		this.init()
		let { type, value } = query
		if(type && value) {
			value = value - 0
			if(type === '+') {
				this.num += value
			} else if(type === '-') {
				this.num -= value
			}
			this.type = type
			this.count = value
			this.startCartoon()
			uni.setStorageSync('leftOb', this.num)
		}
	},
	
	onUnload() {
		
	}
}
